<template><section>
<h2>Icon 图标</h2>
<p>Ruler的图标使用的是开源项目<a href="http://ionicons.com/"><RuIcon type="ionic"></RuIcon> ionicons</a></p>
<h3>使用方法</h3>
<coding title="图标" class="icon-examples" note="使用Icon组件，只要指定图标对应的type属性即可，同时可设置color和size属性控制图标的颜色及大小" lang="html" :code="type">
  <RuIcon type="heart" size="30px"></RuIcon>
  <RuIcon type="heart-broken" size="30px"></RuIcon>
  <RuIcon type="social-github" size="30px"></RuIcon>
  <RuIcon type="social-twitter" color="#2d8cf0" size="30px"></RuIcon>
</coding>
<h3>Icon Props</h3>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>size</td>
<td>图标的大小，单位px</td>
<td>Number／String</td>
<td>-</td>
</tr>
<tr>
<td>type</td>
<td>图标名称</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>color</td>
<td>图标颜色</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3>图标集合</h3>
<ul class="icon-list">
  <li v-for="type in icons" :key="type">
    <div>
      <RuIcon :type="type" :size="'32px'"></RuIcon>
    </div>
    <p>
      <span class="icon-type">{{type}}</span>
    </p>
  </li>
</ul>
</section></template>
<style lang="scss" scoped>
i {
  color: #5a6b76;
}
a {
  text-decoration: none;
  &:active,
  &:link,
  &:visited {
    color: #008cec;
    i {
      color: #008cec;
    }
  }
  &:hover {
    color: #5cadff;
    i {
      color: #5cadff;
    }
  }
}
ul {
  display: flex;
  flex-wrap: wrap;
  margin-top: 50px;
  list-style: none;
  li {
    width: 16.6%;
    height: 100px;
    > div {
      height: 60px;
      line-height: 60px;
      text-align: center;
    }
    > p {
      height: 40px;
      text-align: center;
    }
  }
}
.icon-examples {
  [class^="ru-icon-"] {
    display: inline-block;
    margin: 0 20px;
  }
}
</style>
<script> 
 let type = 
 ` <RuIcon type="heart" size="30px"></RuIcon>
  <RuIcon type="heart-broken" size="30px"></RuIcon>
  <RuIcon type="social-github" size="30px"></RuIcon>
  <RuIcon type="social-twitter" color="#2d8cf0" size="30px"></RuIcon>`
  const iconList = require('@/icon.json')
  export default {
    data() {
      return {
        icons: iconList,
        type: type
      }
    }
  }
</script>


